<template>
    <div class="content fr">
        <!-- positio -->
        <div class="position">
            <router-link to='/'>{{lang.home}}</router-link>
            <span> > </span>
            <router-link :to="langType!='cn'?('/'+langType+'/personageNew'):'/personageNew'">{{$route.meta.title[langType]}}</router-link>
        </div>
        <!-- content -->
        <div class="list">
            <div class="title">
                <h2 class="name">{{title}}</h2>
                <p class="summarty">
                    <span>{{lang.publishTime}} :</span>
                    <span>{{gmtCreate | formateDate}}</span>
                    <span>{{lang.autor}} :</span>
                    <span>{{autorName}}</span>
                </p>
            </div>
            <div class="content-txt" v-html="conetnt">
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'list',
    data() {
        return {
            /***********article***********/
            article: false,
            conetnt: '',  //详情
            gmtCreate: '',//发布时间
            autorName: '',//作者
            Source: '',//来源
            id: '',
            title: '',
            formatContent: '',
            selected: 0,
            langType:"cn",
            lang:{},
        }
    },
    filters: {   //过滤
        formateDate: function (value) {
            var d = new Date(value);
            var y_ = d.getFullYear();
            var m_ = parseInt(d.getMonth() + 1) < 10 ? '0' + parseInt(d.getMonth() + 1) : parseInt(d.getMonth() + 1);
            var d_ = parseInt(d.getDate()) < 10 ? '0' + parseInt(d.getDate()) : d.getDate();
            return y_ + "-" + m_ + '-' + d_;
        }
    },
    mounted: function () {
        this.$nextTick(function () {
            this.toggleLang();
            this.getDetail(this.$route.params.id);
        });
    },
    watch:{
        "$route.fullPath":{
            handler(){
                this.toggleLang();
                this.getDetail(this.$route.params.id);
            }
        }
    },
    methods: {
        getDetail: function (params) {
            /*详情*/
            var url = "/api/front/news/" + params, that = this;
            this.commonMethods.emitAjax({
                path: url,
                type: "GET",
                data: {},
                headers: null,
                success: function (response) {
                    if (response) {
                        that.title = response.title;
                        that.conetnt = response.content;
                        that.gmtCreate = response.gmtCreate;
                        that.autorName = response.autorName;
                        that.Source = '';
                    }
                }
            })
        },
        toggleLang(){
            const _this = this;
            this.commonMethods.selectLangType(function(langType){
                _this.lang = _this.langOption[langType];
                _this.langType = langType;
            })
        }
    }
}
</script>


